/*  
	js파일 불러오는 법 : 
	1. src폴더 안에 파일생성후 하단에 변수명이나 함수명을 export default data;
	export {a, b}; 익스포트하고싶은 변수, 여러개면 중가로로 묶고 default는 안써도 된다.
	export default a 한개일때
	함수도 익스포트 가능
	export default data;
	다른 js.폴더에서 방대한 자료를 사용하고 싶을때. 중가로로 가져올땐 작명을 할수 없고 중가로로 가져와야함.{a,b}
	2. App.js파일에 import data from './data' .js생략가능
	경로쓸때는 ./ 이거로 먼저시작해야함
	3. let [shose, setShose] = useState(data); 다른 변수명 설정
*/

import { Routes, Route, Link, useNavigate, Outlet, useParams } from 'react-router-dom'; // ./로 시작하는것들은 내가만든것, 'react-router-dom' 이런건 라이브러리, 사용할거(useParams같은) 입력하고 엔터치면 자동으로 입력됨.
/*
	라우터 사용법 : react-router-dom 사용
	1. 터미널에서 npm install react-router-dom@6 라이브러리 설치
	2. src/index.js파일에서 <App />을 <BrowserRouter></BrowserRouter>로 감싸주고 상단에 임포트 import { BrowserRouter } from 'react-router-dom';
	3. App.js에서 임포트 import { Routes, Route, Link } from 'react-router-dom';
	4. className="App"안에 라우츠태그 넣어서 사용하기
		<Routes>
			<Route path="/" element={<div>메인페이지임</div>}></Route> 메인페이지면 슬래시 하나
			<Route path="/detail" element={<div>상세페이지임</div>}></Route> 패스안에 페이지 경로명 작명, element 안에 코딩
			<Route path="/about/ element={<div>어바웃페이지임</div>}>위랑같음
		</Routes>
*/
import axios from 'axios'
/*
	ajax 쓰려면 옵션 3개중 택1
		1. XMLHttpRequest
		2. fetch()
		3. axios 같은거 -> 라이브러리. 간단하고 코드가 더 짧아진다.
	
	axios 사용순서
		1. 터미널에 npm install axios 
		2. 상단에 임포트하기 import axios from 'axios'
		3. 버튼 onClick function 안에 
			onClick={()=>{
				axios.get('https://codingapple1.github.io/shop/data2.json')
				.then((결과)=>{
					console.log(결과) //여러정보가 담김
					console.log(결과.data) //핵심정보를 확인하고 싶을때
					원래는 문자로 받아오는데 axios는 자동으로 json으로 변환해준다.
					자바스크립트로 불러올때는 .json()으로 변환을 해줘야함.
				})
				.catch(()=>{ //ajax요청 실패했을때의경우. 주소를 잘못썼거나
					console.log('실패함 ㅅㄱ')
				})
			}}

	javascript fetch() 사용시 상단에 라이브러리 안걸어도 된다.
		fetch('https://codingapple1.github.io/shop/data2.json')
			.then(res => res.json()) //json으로 변환해줌. 그냥 문자로 사용하고싶으면 안써도 됨
			이 과정이 귀찬기때문에 axios를 사용한다고함.
			.then(dataaa => {
				console.log(dataaa)
			})
			.catch(error =>{
				console.log(error)
			})

	↓알고 넘어가면 좋은것들
	서버로 데이터 전송하는 POST요청(백단인듯)
	axios.post('/sdfsdfsf', {name : 'kim'})

	ajax를 동시에 여러개 요청하는 경우
	Promise.all([ axios.get('/url1'), axios.get('/url2') ])
	.then(()=>{
		두개동시에 가져오는것을 성공했을때 실행할 코드
	})
*/

import { useQuery } from 'react-query';

export let Context1 = createContext()
/*
	리액트 기본문법 Context API 사용법
	1. App()밖에 변수 생성 
		- export let Context1 = createContext()
		- Context1 얘가 보관함임
		- 추출해야하니까 export
	2. <context>로 원하는 컴포넌트 감싸기
		ex)
			<Route path="/detail/:id" element={
				<Context1.Provider value={{ 재고, shose }}> <- 사용할 스테이트들 넣기
					<Detail shose={shose}/> <- 여기 안의 모든 컴포넌트는 재고, shose 사용가능
				</Context1.Provider>
			}></Route>
	3. 사용할 컴포넌트파일에서 임포트
		- import { Context1 } from './../App.js';
		- 임포트에 훅 추가 : let Detail안에 useContext엔터
		- let context = useContext(Context1); <- 해체해달라는것
		  콘솔찍으면 스테이트가 오브젝트형으로 들어가있다.

	장점
	1. Detail뿐만 아니라 Detail의 자식들도 props 없이 사용가능 
		- 그러려면 해당 컴포넌트 안에 
		  let context = useContext(Context1); <- 얘 추가해야한다.
		
*/


