const tabArr = [
	<div>contents 1</div>,
	<div>contents 2</div>,
	<div>contents 3</div>
]

const Tabcont = (props)=>{
	let [fade, setFade] = useState('')
	useEffect(()=>{
		let a = setTimeout(()=>{
			setFade('end')
		}, 100)
		return ()=>{
			clearTimeout(a);
			setFade('')
		}
	},[props.tab])
	return (
		<div className={`start ${fade}`}>
			{ [<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][props.tab] }
			{/* 이렇게도 됨 중괄호로 감싸기  */}
		</div>
	)
	/*
		리액트의 automatic batching 기능
		스테이트 변경함수가 근처에 있으면 쓸때마다 변경해주는것이 아니라 
		합쳐줘서 마지막에 남는거 한번만 변경해주는 기능으로 업데이트 됨
		그래서 setTimeout함수를 사용해야한다.
	*/
}

