import { configureStore, createSlice } from '@reduxjs/toolkit'
import user from './store/userSlice.js'

let id = createSlice({
	name : 'id',
	initialState : 'kim'
})




let stock = createSlice({
	name : 'stock',
	initialState : [10,11,12]
})

let cart = createSlice({
	name : 'cart',
	initialState : [
		{id : 0, name : 'White and Black', count : 2},
		{id : 2, name : 'Grey Yordan', count : 1}
	],
	reducers : {
		plusCount(state, action){
			let num = state.findIndex((el)=>{ return el.id == action.payload })
			state[num].count++
		},
		addCart(state, action){
			let find = state.filter((el)=>{
				return el.id == action.payload.id
			})
			if(find.length == 0){
				state.push(action.payload)
			} else {
				const test1 = state.findIndex((item) => item.id === action.payload.id);
				state[test1].count++
			}
		},
		delCart(state, action){
			const test2 = state.findIndex((item) => item.id === action.payload.id);
			state.splice(test2,1)
		},		
	}
})



export default configureStore({
  reducer: { 
	id : id.reducer,
	user : user.reducer,
	stock : stock.reducer,
	cart : cart.reducer,
  }
}) 

export let {plusCount, addCart, delCart} = cart.actions