import { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Nav } from "react-bootstrap";
import { useDispatch, useSelector } from 'react-redux';
import { addCart } from "./../store.js";
import { Context1 } from './../App.js';
import styled from 'styled-components';
/*
	styled-components 사용방법

	1. npm install styled-components 터미널에 설치
	2. import styled from 'styled-components'; 임포트
	3. 컴포넌트 생성자리에(App()밖에) let YellowBtn = styled.button``
	4. <YellowBtn>버튼</YellowBtn>

	장점
	1. CSS파일 안열어도 된다. 
	2. 모든파일을 다 합쳐주기때문에 스타일이 다른 js파일로 오염되지 않음
		- 방지법 CSS파일 작명할때 App.css파일을 App.module.css로 작명하면(컴포넌트.module.css) App.js에만 반영. (Detail.module.css는 Detail.js에만 반영)
	3. 페이지 로딩시간 단축. <style></style>로 넣어주기 때문에 페이지 로딩시간 향상

	단점
	1. JS파일 매우 복잡해짐(컴포넌트 구분면에서)
	2. 중복스타일은 컴포넌트간 exprt, import 해야한다.
	3. 협업시 CSS 담당의 숙련도 이슈
*/

//실은 스타일을 입힌 컴포넌트를 생성한 것임. 그래서 대문자 먼저
//props전송도 가능 ${ props => props.작명 }
/* ↑외부 라이브러리 사용법이라 이해보다는 그냥 복붙 */
//<YellowBtn 작명="blue">버튼</YellowBtn>
// 고유의 쿨래스명이 자동 생성됨
let YellowBtn = styled.button`
	background : ${ props => props.bg };
	color : ${ props => props.bg == 'blue' ? 'white' : null };
	padding : 10px;
`
// 기존 스타일 복사가능 ↓얘안됨
// let NewBtn = styled.button(YellowBtn)`
// 	border-radius : 15px;
// `

let Box = styled.div`
	background: grey;
	padding : 20px;
`

let Detail = (props) => {
	let context = useContext(Context1);
	console.log(context);
	let {id} = useParams();
	//parseInt({id, odd})
	// 유저가 URL파라미터에 입력하고 가져오려면 사용, App.js말고 해당 컴포넌트 함수안에 사용
	let [swt, setSwt] = useState(true);
	let [ipt, setIpt] = useState("");
	let [count, setCount] = useState(0);
	let [addClass, setAddClass] = useState('');
	let state = useSelector((state)=> state )
	let dispatch = useDispatch()
	let [cnt, setCnt] = useState(0)
	// let [locst, setLocst] = useState('');
	/* 
		컴포넌트의 Lifecycle
		mount : 페이지에 장착되기도 하고
		update : 가끔 업데이트도 되고
		unmount : 필요없으면 제거되고(필요없는페이지에서는 제거되듯이)
		(컴포넌트는 이런 인생주기를 겪음)

		↓예전방식
		class Detail2 extends React.Component {
			componentDidMount(){
				컴포넌트 Mount시 여기코드 실행됨
			}
			componentDidUpdate(){
				컴포넌트 Update시 여기코드 실행됨
			}
			componentWillUnmount(){
				컴포넌트 Unmount시 여기코드 실행됨
			}
		}

		↓요즘방식 - 컴포넌트 function안에
		useEffect(()=>{
			mount, update시 여기코드 실행됨, html을 먼저 보여주고 실행한다.
		})
		useEffect 안에 적는 코드들은 :
			- 어려운 연산
			- 서버에서 데이터 가져오는 작업
			- 타이머 장착하는거
			- 내생각엔 자바스크립트에서 로드펑션 사용할때와 같은 용도로 사용
		Side Effect : 함수의 핵심기능과 상관없는 부가기능
	*/

	useEffect(()=>{
		let a = setTimeout(()=>{
			setSwt(false)
		}, 2000);

		if(isNaN(ipt) == true){
			alert('숫자입력해라')
		} else {

		}
		return ()=>{
			clearTimeout(a)
		}
	})

	useEffect(()=>{
		//setTimeout(()=>{
			setAddClass('opa1')
		//},100)
		// return ()=>{
		// 	setAddClass('')
		// }
	}, [])

	/*
		useEffect(()=>{
			mount, update시 여기코드 실행됨, html을 먼저 보여주고 실행한다.
		})

		useEffect(()=>{
			마운트시와 해당변수가 변할때마다 실행하는 함수, 다른함수가 변할땐 실행안된다.
		}, [스테이트명, 스테이트명]) <-디펜던시??

		useEffect(()=>{
			마운트에만 실행됨, 업데이트시엔 실행되지않음 (편법), 
			컴포넌트 마운트시 1회만 실행하고싶으면 이렇게
		}, [])

		useEffect(()=>{
			let a = setTimeout(()=>{ setSwt(false) }, 2000);
			console.log(2)
			return ()=>{
				clean up function : useEffect 동작 전에 실행됨
				기존코드 치우는거 여기에 많이 작성함
				마운트시 실행안됨. 언마운트시 사용됨.
				clearTimeout(a)
				console.log(1)
			}
		})

		useEffect(()=>{  }) 		1. 재렌더링마다 코드실행
		useEffect(()=>{  }, []) 	2. 마운트시 코드 1회 실행
		useEffect(()=>{  }, [변수]) 3. 마운트시, 해당변수 변경시
		useEffect(()=>{ 			4. unmount시 코드 1회 실행
			return ()=>{
				코드
			}
		}, [])
									5. useEffect 실행 전에 뭔가 실행하려면 언제나
									return ()=>{} 얘는 마운트시엔 동작 안함.
	*/


	let filt = props.shose.find((a)=>{
		//parseInt(a.id)
		return(a.id == id)
	})
	useEffect(()=>{
		// locst = [...locst]
		// locst.push(id)
		// setLocst(locst)
		// console.log(locst)
		let getting = JSON.parse(localStorage.getItem('watched'));
		let check = getting.findIndex((el)=> el == id)
		//이런방법도 있다 -> array - Set - array
		//getting = new Set(getting) -> 중복없앤 어레이자료, 중괄호
		//getting = Array.from(꺼낸거) -> 다시 어레이로
		if(check === (-1)){
			getting.push(id)
			localStorage.setItem('watched',JSON.stringify(getting))
		} else {
			return
		}

	},[])
	// let 찾은상품 = props.shoes.filter(function(x){
	// 	return x.id == id
	// }); find의 결과는 오브젝트고 filter의 결과는 어레이다. find는 해당하는 첫번째 결과만 오브젝트로 꺼내준다. filter로 사용한다면 아래에 데이터 바인딩할때 filt[0].id로 써줘야함.
	//parseInt(filt[0].id)

	// let tt = [0,5,2,6,3,5]
	// tt = new Set(tt)
	// tt = Array.from(tt)
	// console.log(tt)

	return(

		<div className={`container opa0 ${addClass}`}>
			<Box>
				<YellowBtn bg="blue">버튼</YellowBtn>
				<YellowBtn bg="skyblue">버튼</YellowBtn>
				<YellowBtn bg="lime">버튼</YellowBtn>
				<YellowBtn bg="orange">버튼</YellowBtn>
			</Box>
			<input type="text" className="test" onInput={(e)=>{setIpt(e.target.value)}}/>
			{/* {
				ipt == true ? alert('안녕하쇼') : null
			} */}
			{
				swt == true ? <div className="alert alert-warning">2초후에 사라지는 혜택</div> : null
			}
			
			{count}
			<button onClick={()=>{
				setCount(count+1)
			}}>+1</button>
			<div className="row">
				<div className="col-md-6">
					<img src={`https://codingapple1.github.io/shop/shoes${filt.id+1}.jpg`} width="100%" />
				</div>
				<div className="col-md-6">
					<h4 className="pt-5">{filt.title}</h4>
					<p>{filt.content}</p>
					<p>{filt.price}원</p>
					<input type="text" onInput={(e)=>{
						setCnt(e.target.value)
					}} />
					<button className="btn btn-danger" onClick={(e)=>{
						dispatch(addCart({ id : filt.id , name : filt.title , count : 1 }))
					}}>주문하기</button> 
				</div>
			</div>
			<Nav variant="tabs"  defaultActiveKey="link0">
				<Nav.Item>
					<Nav.Link eventKey="link0">버튼0</Nav.Link>
				</Nav.Item>
				<Nav.Item>
					<Nav.Link eventKey="link1">버튼1</Nav.Link>
				</Nav.Item>
				<Nav.Item>
					<Nav.Link eventKey="link2">버튼2</Nav.Link>
				</Nav.Item>
			</Nav>
			<div>내용0</div>
			<div>내용1</div>
			<div>내용2</div> 
		</div> 
	)
}

export default Detail;