import {Table} from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux';
import { changeName,changeAge } from "./../store/userSlice.js"
import { plusCount, delCart } from "./../store.js"

const Cart = ()=>{

	let state = useSelector((state)=> state )
	//let state = useSelector((state)=>{ return stock.user }) 원하는것만 가져다 쓸수있음
	let dispatch = useDispatch()
	console.log(state)

	return(
		<div>
			유저명 : {state.user.name} <br />
			나이 : {state.user.age}
			<Table>
				<thead>
					<tr>
						<th>#</th>
						<th>상품명</th>
						<th>수량</th>
						<th>변경하기</th>
						<th>삭제버튼</th>
					</tr>
				</thead>
				<tbody>
					{
						state.cart.map((el, i)=>
							<tr key={i}>
								<td>{state.cart[i].id}</td>
								<td>{state.cart[i].name}</td>
								<td>{state.cart[i].count}</td>
								<td><button onClick={()=>{
									// dispatch(changeName())
									// dispatch(changeAge(100))
									dispatch(plusCount(state.cart[i].id))
								}}>+</button></td>
								<td><button onClick={()=>{
									// dispatch(changeName())
									// dispatch(changeAge(100))
									dispatch(delCart(state.cart[i].id))
								}}>삭제</button></td>
							</tr>
						)
					}
				</tbody>
			</Table> 
		</div>
	)
}

export default Cart;

/*
	Redux 사용하기 - 컴포넌트들이 props없이 state공유가능
	컴포넌트간 state 공유 편해짐
	패키지.json에서 리액트 버전이 리액트 버전이 18.1 이상이어야함


	셋팅방법----------------------------------------------------------
	1. npm install @reduxjs/toolkit react-redux 설치
	2. src폴더 안에 store.js파일 생성 -> state저장통
		- 파일안에 복붙
			import { configureStore } from '@reduxjs/toolkit'

			export default configureStore({
				reducer: { 

				}
			}) 
	3. index.js에서 
		- 프로바이더, store 임포트
			import { Provider } from "react-redux"
			import store from './store.js'
		- 프로바이더로 앱을감싸기
			<Provider store={store}> <- 스토어.js 임포트한거
				<BrowserRouter>
					<App /> <- store.js 안의 state는 전부 사용가능
				</BrowserRouter>
			</Provider>
	4. 셋팅 끝
	------------------------------------------------------------------

	사용방법 - store의 state꺼내는 법
	1. state 만드는법 - store.js에서 실행
		- createSlice엔터
		import { configureStore, createSlice } from '@reduxjs/toolkit'
		- 
			let id = createSlice({
				name : 'id',
				initialState : 'kim'
			}) <- state하나를 slice라고 부름

			let stock = createSlice({
				name : 'stock',
				initialState : [10,11,12]
			})
		- 변수명을 하단 reducer에 등록

		export default configureStore({
			reducer: { 
				id(작명부분, 보통 같게작명) : id.reducer,
				user : user.reducer,
				stock : stock.reducer,
				cart : cart.reducer,
			}
		})

	2. 사용해야하는 컴포넌트에서 - Cart.js에서 실행
		- useSelector엔터(상단에 임포트)
		- 카트펑션 안에 let state = useSelector((state)=> state ) <- 기본문법, 이렇게 시작. store안의 모든 state. 
		((state)=> state.id ) 이렇게 하면 해당 값만 가져온다.

	3. <td>{state.cart[i].id}</td> 사용하면 됨.
	------------------------------------------------------------------

	Redux의 state 변경하는법
	1. 슬라이스 안에 reducers 생성, 그 안에 함수 여러개 생성 가능 - store.js에서 실행
		- 생성된 함수들은 action이라고 한다. 그래서 파라미터명도 보통 action
		let cart = createSlice({
			name : 'cart',
			initialState : [
				{id : 0, name : 'White and Black', count : 2},
				{id : 2, name : 'Grey Yordan', count : 1}
			],
			reducers : {
				changeName(state){ <- 파라미터 사용가능
					return 'john ' + state
				},
				plusCount(state, action){
					let num = state.findIndex((el)=>{ return el.id == action.payload })
					state[num].count++
				},
				addCart(state, action){
					let find = state.filter((el)=>{
						return el.id == action.payload.id
					})
					if(find.length == 0){
						state.push(action.payload)
					} else {
						const test1 = state.findIndex((item) => item.id === action.payload.id);
						state[test1].count++
					}
				},
				delCart(state, action){
					const test2 = state.findIndex((item) => item.id === action.payload.id);
					state.splice(test2,1)
				},		
			}
		})

	2. 만든 함수 export 해야함 - store.js에서 실행
		- 하단에 
		cart.actions <- 함수가 object형식으로 남는다.
		export let {plusCount, addCart, delCart} = cart.actions
		관습적으로 사용하고 싶은 함수를 다 적는다. 오른쪽자료를 변수로 빼는 문법일뿐
		디스트럭쳐링

	3. Cart.js에서 import
		- useDispatch엔터 후 변수
		import { useDispatch, useSelector } from 'react-redux';
		let dispatch = useDispatch()
		- 사용원하는 함수명 import
		import { plusCount, delCart } from "./../store.js"

	4. Cart.js에서 원하는 곳에 dispatch로 감싼후 사용
		-
			<td><button onClick={()=>{
				dispatch(delCart(state.cart[i].id))
			}}>삭제</button></td>

	5. array/object의 경우 직접 수정해도 state변경된다.(immer.js가 자동 설치되어서)
		-
			changeName(state){
				state.name = 'park'
			},

	6. state 변경함수에 파라미터 뚫는법
		- 파라미터 뒤에 .payload를 붙인다.
			increase(state, action){
				state.age+=action.payload
			}
		- dispatch(changeAge(100)) <- 스토어에 요청하는것임. 바꿔달라고. 100이란 메세지를 싣고
		- 파라미터 작명은 보통 action으로 한다.
	--------------------------------------------------------------------

	파일 분할하는법
	1. 파일 생성후 해당파일 상단에 import
		- import { configureStore, createSlice } from '@reduxjs/toolkit'
	
	2. 해당파일 하단에 export 두개(함수사용을 위한거, 스테이트를 담은 슬라이스를 store.js로 reducer등록을 위해)
		- export let {changeName,changeAge} = user.actions
		- export default user

	3. store.js에서 import
		- import user from './store/userSlice.js'
*/